@import "_normalize";
@import "_variables";
@import "_hero.scss";
@import "_skills.scss";
@import "_projects";
@import "_footer";

@import url('https://fonts.googleapis.com/css?family=Poppins:300,400,500,600&display=swap&subset=latin-ext');


html {
  box-sizing: border-box;
  font-family: 'Poppins', sans-serif;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  background: $white;
}

.main {
  margin: 0;
}