.hero {
  margin-top: 4rem;
  padding: 0 1rem; }
  .hero__image {
    margin: 0;
    margin-bottom: 1rem;
    max-width: 100px;
    max-height: 100px;
    border-radius: 50%;
    border: 4px solid #FFFFFF;
    box-shadow: 0px 0px 0px 6px #CBD2D9; }
  .hero__title {
    margin: 0;
    margin-bottom: 0.5rem;
    font-size: 1.625rem;
    font-weight: bold;
    line-height: 2.25rem;
    color: #171F3F; }
  .hero__paragraph {
    margin: 0;
    margin-bottom: 2.5rem;
    font-size: 0.875rem;
    font-weight: 300;
    line-height: 1.625rem;
    color: #52606D; }
  .hero__link {
    font-weight: 500;
    color: #0593FB;
    cursor: pointer; }
  .hero__email {
    position: relative;
    margin: 0;
    margin-bottom: 7.8rem;
    font-size: 1rem;
    font-weight: 500;
    line-height: 1.6rem;
    color: #0593FB; }
    .hero__email::after {
      content: "";
      position: absolute;
      bottom: -15%;
      left: 0;
      height: 2px;
      width: 218px;
      background-color: #0593FB; }

.skills {
  margin-bottom: 5rem;
  padding: 0 1rem; }
  .skills__title {
    margin: 0 auto;
    font-size: 1.5rem;
    font-weight: 600;
    line-height: 2rem;
    text-align: center;
    color: #171F3F;
    margin-bottom: 6rem;
    position: relative; }
    .skills__title::after {
      content: "";
      position: absolute;
      bottom: -35%;
      left: 50%;
      transform: translateX(-50%);
      height: 2px;
      width: 100%;
      background-color: #CBD2D9;
      opacity: 0.3; }
  .skills__subtitle {
    margin: 0 auto;
    font-size: 1.375rem;
    font-weight: 600;
    line-height: 2rem;
    text-align: left;
    color: #171F3F;
    margin-bottom: 0.5rem;
    position: relative; }
    .skills__subtitle::before {
      content: "";
      position: absolute;
      top: -25%;
      left: 0;
      width: 40px;
      height: 4px;
      border-radius: 8px;
      transform: matrix(1, 0, 0, -1, 0, 0); }
    .skills__subtitle--green::before {
      background-color: #06C847; }
    .skills__subtitle--purpure::before {
      background-color: #653CAD; }
    .skills__subtitle--darkerBlue::before {
      background-color: #2251CC; }
  .skills__list {
    margin: 0 auto;
    margin-bottom: 3.5rem;
    list-style: none;
    padding: 0;
    margin-left: 8px; }
  .skills__item {
    font-size: 0.875rem;
    font-weight: 400;
    line-height: 2rem;
    color: #52606D;
    position: relative;
    padding-left: 0.85rem; }
    .skills__item::before {
      content: "";
      position: absolute;
      top: 25%;
      left: -0.5rem;
      height: 12px;
      width: 12px; }
    .skills__item--green::before {
      background-image: url(../assets/img/icons/iconGreen.svg); }
    .skills__item--purpure::before {
      background-image: url(../assets/img/icons/iconPurpure.svg); }
    .skills__item--blue::before {
      background-image: url(../assets/img/icons/iconBlue.svg); }

.projects {
  padding: 0 1rem 5rem 1rem;
  background-color: #F5F7FA; }
  .projects__title {
    margin: 0 auto;
